import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'pxt-upload-file',
  templateUrl: './pxt-upload-file.component.html',
  styleUrls: ['./pxt-upload-file.component.css']
})
export class PxtUploadFileComponent implements OnInit {

  @Input() placeholder: string;
  @Output() fileSelected: EventEmitter<any> = new EventEmitter();
  constructor() { }

  arrayImages: FileReader;

  ngOnInit() {
  }
  onChangeImagem(event) {
    if (event != undefined) {
      let imagem: File = event;
      this.placeholder = imagem.name;
      return this.fileSelected.next(imagem);
    }

  };
}
